import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./desktopNav.scss";

import MobileNav from "./mobileNav"

class DesktopNav extends Component {
	state = {
		title: "Home"
	}
	
	setActive = element =>{
		this.refs.Home.classList.remove("active")
		this.refs.About.classList.remove("active")
		this.refs.Project.classList.remove("active")
		this.refs.Contact.classList.remove("active")
		element.classList.add("active")
	}

	openCloseMobileMenu = () => {
		const nav = document.getElementById("root").classList;
		if (nav.contains("activeMobileMenu")) nav.remove("activeMobileMenu");
		else nav.add("activeMobileMenu");
	};
	
	render() {
		return (
			<nav id="desktopNav">

				<MobileNav openCloseMobileMenu={this.openCloseMobileMenu} />

				<ul id="base">
					<Link to="/">
						<li>
							<div id="logo"></div>
						</li>
					</Link>
					<li>
						<Link to="/">
							<h1 id="siteName">Theophilus</h1>
						</Link>
					</li>
					<li className="gap"></li>

					<ul id="lv1">
						<Link to="">
							<li className="navBtn active" ref="Home" onClick={() => this.setActive(this.refs.Home)} >Home</li>
						</Link>
						<Link to="/about">
							<li className="navBtn" ref="About" onClick={() => this.setActive(this.refs.About)} >About Me</li>
						</Link>
						<li className="navBtn"  ref="Project" >
							Projects
							<ul id="lv2" className="lv2">
								<Link to="/projects#web">
									<li className="navBtn" onClick={() => this.setActive(this.refs.Project)} >Web</li>
								</Link>
								<Link to="/projects#logo">
									<li className="navBtn" onClick={() => this.setActive(this.refs.Project)}>Logos and Banners</li>
								</Link>
								<Link to="/projects#model">
									<li className="navBtn" onClick={() => this.setActive(this.refs.Project)}>3D</li>
								</Link>
								<Link to="/projects#animation">
									<li className="navBtn" onClick={() => this.setActive(this.refs.Project)}>Animation</li>
								</Link>
								<Link to="/projects#others">
									<li className="navBtn" onClick={() => this.setActive(this.refs.Project)}>Electronics</li>
								</Link>
							</ul>
						</li>
						<Link to="/contact" >
							<li className="navBtn" ref="Contact" onClick={() => this.setActive(this.refs.Contact)} >Contact</li>
						</Link>
					</ul>

					<li>
						<div
							onClick={this.openCloseMobileMenu}
							className="navBtn"
							id="mobileMenuButton"
						></div>
					</li>
				</ul>
			</nav>
		);
	}
}

export default DesktopNav;
