import React from "react";
import { Link } from "react-router-dom";
import "./mobileNav.scss";

class MobileNav extends React.Component {	

	setActive = element =>{
		this.refs.Home.classList.remove("active")
		this.refs.About.classList.remove("active")
		this.refs.Project.classList.remove("active")
		this.refs.Contact.classList.remove("active")
		element.classList.add("active")

		document.getElementById("root").classList.remove("activeMobileMenu");
	}


	render() {
		return (

			<div id="mobileNav">
				<div id="blocker" onClick={this.props.openCloseMobileMenu} >
				</div>

				<ul id="panel">

					<ul id="lv1_1">
						<Link to="">
							<li className="navBtn active" ref="Home" onClick={() => this.setActive(this.refs.Home)} >Home</li>
						</Link>
						<Link to="/about">
							<li className="navBtn" ref="About" onClick={() => this.setActive(this.refs.About)} >About Me</li>
						</Link>
						<Link to="/projects">
							<li className="navBtn"  ref="Project"  onClick={() => this.setActive(this.refs.Project)} >Projects</li>
						</Link>
						<Link to="/contact" >
							<li className="navBtn" ref="Contact" onClick={() => this.setActive(this.refs.Contact)} >Contact</li>
						</Link>
					</ul>


					<ul id="footer">
						<li>08067397793</li>
						<li>theophilus.ogbolu@gmail.com</li>
						<li>Lagos.</li>
						<hr/>
						<ul className="social" >
							{/* <a href="assets/file/theophilus_cv.pdf"  className="cv btn" download>Download CV</a> */}
							{/* <p className="space"></p> */}
							<a href="https://wa.me/2348067397793/?text=Hello%20Theophilus"><li className="whatsapp"></li></a>
							<a href="https://m.facebook.com/Pheezie" target="_blank" rel="noopener noreferrer"><li className="facebook"></li></a>
							<a href="https://twitter.com/Pheezie" target="_blank" rel="noopener noreferrer"><li className="twitter"></li></a>
							<a href="https://www.linkedin.com/in/theophilus-ogbolu-977385136/" target="_blank" rel="noopener noreferrer"><li className="linkedin"></li></a>
							<a href="https://github.com/pheezie" target="_blank" rel="noopener noreferrer"><li className="github"></li></a>
						</ul>
					</ul>
				</ul>
			</div>

		);
	}
}

export default MobileNav;
